<template>
  <div>
    <NavTemplent title=""></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold"></h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
          <el-form-item label="">
            <el-select v-model="form.departmentId" placeholder="">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.site" placeholder="">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.channel" placeholder="">
              <el-option label="FaceBook" :value="0"></el-option>
              <el-option label="google" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              </el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">lastchic</h5>
          <div id="myChart3" style="width: 100%;height: 300px"></div>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">
            ROI 
          </h5>
          <el-tabs v-model="activeName">
            <el-tab-pane label="" name="personal">
              <el-table
                :data="promotionList"
                border
                style="width: 100%;height: 250px">
                <el-table-column
                  type="index">
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.department_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.role_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="ROI">
                  <template slot-scope="scope">
                    <span>{{scope.row.status}}</span>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="" name="department">
              <el-table
                :data="promotionList"
                border
                style="width: 100%;height: 250px">
                <el-table-column
                  type="index">
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.department_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.role_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="ROI">
                  <template slot-scope="scope">
                    <span>{{scope.row.status}}</span>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div></div>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-download"></i></el-button>
        </span>
      </div>
      <el-table
        :data="promotionList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.role_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="ROI">
          <template slot-scope="scope">
            <span>{{scope.row.status}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editUseMessage(scope.row)"></el-button>
            <el-button type="danger" size="mini" @click="delUseMessage(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.cost" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="ROI">
            <el-input v-model="form.email" placeholder="ROI"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.department_id" placeholder="">
              <el-option label="FaceBook" :value="0"></el-option>
              <el-option label="google" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle"> </el-button>
          <el-button size="mini" type="primary" @click="save"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                activeName: 'personal',
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                siteList: null,
                dialogVisible: false,
                promotionList: [],
                total: 0,
                title: '',
                form: {
                    site: '',
                    date: [],
                    departmentId: '',
                    password: '',
                    cost: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine3() {
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart3'));
                // 
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['','','','']
                    },
                    series: [
                        {
                            name:'',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:''},
                                {value:335, name:''},
                                {value:310, name:''},
                                {value:234, name:''}
                            ]
                        }
                    ]
                });
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            editUseMessage(data) {
                this.dialogVisible = true;
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {}
        },
        mounted() {
            this.getSiteList();
            this.getDepartmentList();
            this.drawLine3()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



