<template>
  <div>
    <NavTemplent title=""></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold"></h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-form-item label="">
            <el-input v-model="form.siteName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getSiteList">
              <i class="el-icon-search"></i>
              </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div></div>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i></el-button>
        </span>
      </div>
      <el-table
        :data="siteList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.website_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.website_type || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.update_time * 1000 | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-link @click="editUseMessage(scope.row)" style="color:#409EFF"></el-link>
            <el-link @click="delUseMessage(scope.row)" style="color:#409EFF"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="siteList.length"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="">
            <el-input v-model="form.website_name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="ID">
            <el-input v-model="form.view_id" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.website_domain" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle"> </el-button>
          <el-button size="mini" type="primary" @click="save"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                shopfiyMessage: false,
                omsMessage: false,
                gaMessage: false,
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                title: '',
                dialogVisible: false,
                siteList: [],
                omSiteMessage: null,
                gaSiteMessage: null,
                form: {
                    view_id:'',
                    website_name: '',
                    siteName: '',
                    website_domain: ''
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            editUseMessage(data) {
                this.dialogVisible = true;
                this.gaSiteMessage = data;
                if (data.website_id) {
                    this.form = data;
                } else {
                    this.form.view_id = '';
                    this.form.website_name = '';
                    this.form.website_domain = '';
                }
            },
            delUseMessage(data) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    if (this.form.siteType === 0) {
                        this.$post('delWebsite', {
                            website_id: data.website_id
                        }).then(() => {
                            this.getSiteList();
                            this.$message.success('OMS')
                        })
                    } else if (this.form.siteType === 2) {
                        this.$post('delGaConfig', {
                            website_id: data.website_id
                        }).then(() => {
                            this.getSiteList();
                            this.$message.success('GA')
                        })
                    }

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.gaSiteMessage.website_id) {
                    this.$post('editGaconfig', {
                        website_id: this.gaSiteMessage.website_id,
                        website_name: this.form.website_name,
                        view_id: this.form.view_id,
                        website_domain: this.form.website_domain
                    }).then(() => {
                        this.$message.success('GA');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getSiteList();
                    });
                } else {
                    this.$post('addGaConfig', {
                        website_name: this.form.website_name,
                        website_domain: this.form.website_domain,
                        view_id: this.form.view_id
                    }).then(() => {
                        this.$message.success('GA');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getSiteList();
                    });
                }
            }
        },
        mounted() {
            this.getSiteList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



