<template>
  <div>
    <NavTemplent title=""></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold"></h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-form-item label="">
            <el-select v-model="form.departmentId" placeholder="">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getindicatorsList">
              <i class="el-icon-search"></i>
              </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div></div>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i></el-button>
        </span>
      </div>
      <el-table
        :data="indicatorsList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.quota_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-link @click="editUseMessage(scope.row)" style="color:#409EFF"></el-link>
            <el-link @click="delUseMessage(scope.row)" style="color:#409EFF"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="">
            <el-select v-model="form.department_id" placeholder="">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.quota_name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" maxlength="100" show-word-limit v-model="form.quota_desc" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle"> </el-button>
          <el-button size="mini" type="primary" @click="save"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                title: '',
                dialogVisible: false,
                indicatorsList: [],
                departmentList: [],
                quotaMessage: null,
                total: 0,
                form: {
                    department_id: '',
                    departmentId: '',
                    quota_name: '',
                    quota_desc: ''
                }
            }
        },
        methods: {
            getindicatorsList() {
                this.$post('/getQuotaList', {
                    department_id: this.form.departmentId,
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum
                }).then(res => {
                    this.indicatorsList = res.list;
                    this.total = res.count;
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            editUseMessage(data) {
                this.dialogVisible = true;
                this.quotaMessage = data;
                if (data.quota_id) {
                    this.form = data;
                } else {
                    this.form.quota_name = '';
                    this.form.quota_desc = '';
                    this.form.department_id = '';
                }
            },
            delUseMessage(data) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$post('delQuota', {
                        quota_id: data.quota_id
                    }).then(() => {
                        this.getindicatorsList();
                        this.$message.success('')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.form.department_id === '') {
                    this.$message.error('');
                    return;
                }
                if (this.form.quota_name === '') {
                    this.$message.error('');
                    return;
                }
                if (this.quotaMessage.quota_id) {
                    this.form.quota_id = this.quotaMessage.quota_id;
                    this.$post('editQuota', this.form).then(() => {
                        this.$message.success('');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getindicatorsList();
                    })
                } else {
                    this.$post('addQuota', this.form).then(() => {
                        this.$message.success('');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getindicatorsList();
                    });
                }

            }
        },
        mounted() {
            this.getDepartmentList();
            this.getindicatorsList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



