<template>
  <div>
    <NavTemplent title=""></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold"></h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
          <el-form-item label="">
            <el-select v-model="form.department_id" placeholder="">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.cycle" placeholder="">
              <el-option :value="0" label=""></el-option>
              <el-option :value="1" label=""></el-option>
              <el-option :value="2" label=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              </el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left"></span>
            <span style="text-align: right;cursor: pointer" @click="openDetails"></span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span></span>
          </p>
          <el-row>
            <el-col :span="18" style="padding: 5px;text-align: center">
              <div id="myChart" style="width: 100%;height: 200px"></div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left"></span>
            <span style="text-align: right;cursor: pointer" @click="openDetails"></span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder"></p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left"></span>
            <span style="text-align: right"></span>
          </p>
          <el-row>
            <el-col :span="18" style="padding: 5px;text-align: center">
              <div id="myChart1" style="width: 100%;height: 200px"></div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="750px">
      <div id="myChart2" style="width: 100%;height: 200px"></div>
      <div>
        <h4></h4>
        <el-table
          :data="listData"
          border
          style="width: 100%">
          <el-table-column
            type="index">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <span>{{scope.row.user_name || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <span>{{scope.row.remark || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="111">
            <template slot-scope="scope">
              <span>{{scope.row.ip || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="112">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="113">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="114">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="115">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="116">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="117">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" type="danger" @click="cancle"> </el-button>
        <el-button size="mini" type="primary" @click="cancle"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                listData: [],
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                dialogVisible: false,
                total: 0,
                title: '',
                form: {
                    cycle: '0',
                    date: [],
                    department_id: ''
                }
            }
        },
        methods: {
            openDetails() {
                this.dialogVisible = true;
                this.$nextTick(() => {
                    this.drawLine2()
                })
            },
            // 
            drawLine() {
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['','','','']
                    },
                    series: [
                        {
                            name:'',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:''},
                                {value:335, name:''},
                                {value:310, name:''},
                                {value:234, name:''}
                            ]
                        }
                    ]
                });
            },
            drawLine1() {
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart1'));
                // 
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['','','','']
                    },
                    series: [
                        {
                            name:'',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:''},
                                {value:335, name:''},
                                {value:310, name:''},
                                {value:234, name:''}
                            ]
                        }
                    ]
                });
            },
            drawLine2() {
                let myChart = this.$echarts.init(document.getElementById('myChart2'));
                myChart.setOption({
                    title: {text: ''},
                    xAxis: {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [574, 932, 604, 934, 290, 1130, 720],
                        type: 'line'
                    }]
                });
            },
            // 
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            }
        },
        mounted() {
            this.getDepartmentList();
            this.drawLine();
            this.drawLine1();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



